<template>
  <div class="nav">
    <router-link to="/" class="logo">
      <div>Mingo</div>
    </router-link>

    <div class="sentence" :class="{ opacity: isOpen }">
      {{ info }}
    </div>

    <ul class="animated" :class="{ show: isOpen }">
      <router-link to="/project/">
        <li>项目</li>
      </router-link>
      <router-link to="/photoshop">
        <li>摄影</li>
      </router-link>
      <router-link to="/contact">
        <li>联系</li>
      </router-link>
      <router-link to="/about">
        <li>关于</li>
      </router-link>
    </ul>

    <div class="container">
      <div class="bt-close icon" :class="{ open: isOpen }" @click="btn">
        <span class="one"></span>
        <span class="two"></span>
        <span class="three"></span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  components: {},
  props: {},
  data() {
    return {
      isOpen: false,
      info: "社会乱象，根源在我",
    };
  },
  computed: {},
  watch: {},
  async created() {
    var that = this;
    let {
      data: { hitokoto },
    } = await this.$get("http://v1.hitokoto.cn?c=d&c=e&c=k&c=h", {
      encode: "json",
      charset: "utf-8",
      max_length: 14,
    });
    that.info = hitokoto;
  },
  mounted() {},
  methods: {
    btn() {
      if (this.isOpen) {
        clearTimeout(isOpenTime);
        this.isOpen = false;
        return;
      }
      this.isOpen = !this.isOpen;
      let _this = this;
      let isOpenTime = setTimeout(function () {
        _this.isOpen = false;
      }, 22000);
    },
  },
};
</script>

