<template>
  <div>
    <div class="input_group">
      <div class="search">
        <el-input
          placeholder="请输入内容"
          v-model="keyword"
          class="input-with-select"
          clearable
          @keyup.enter.native="search(select)"
        >
          <el-select
            v-model="select"
            slot="prepend"
            placeholder="开发者搜索"
            style="
              width: 7.5rem;
              text-align: center;
              color: #585a57;
              font-weight: 700;
              user-select: none;
            "
          >
            <el-option
              v-for="item in website"
              :key="item.value"
              :label="item.label"
              :value="item.value"
              :disabled="item.disabled"
              style="
                heigth: 2rem !important;
                line-heigth: 2rem !important;
                user-select: none;
              "
            >
              <span style="float: left">{{ item.label }}</span>
              <div style="float: right; padding-top: 2.25px">
                <img :src="item.icon" />
              </div>
            </el-option>
          </el-select>
          <el-button
            slot="append"
            icon="el-icon-search"
            @click="search(select)"
          ></el-button>
        </el-input>
      </div>
      <div class="searchForPhone"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  components: {},
  props: {},
  data() {
    return {
      keyword: "",
      select: "",
      website: [
        {
          label: "开发者搜索",
          value: "",
        },
        {
          label: "百度",
          value: "baidu",
          icon: "icon/baidu.svg",
        },
        {
          label: "搜狗",
          value: "sougou",
          icon: "icon/sougou.svg",
        },
        {
          label: "微信",
          value: "weixin",
          icon: "icon/weixin.svg",
        },
        {
          label: "知乎",
          value: "zhihu",
          icon: "icon/zhihu.svg",
        },
        {
          label: "微博",
          value: "weibo",
          icon: "icon/weibo.svg",
        },
        {
          label: "豆瓣",
          value: "douban",
          icon: "icon/douban.svg",
        },
        {
          label: "谷歌",
          value: "Google",
          icon: "icon/google.svg",
          disabled: true,
        },
      ],
    };
  },
  computed: {},
  watch: {},
  created() {},
  mounted() {},
  methods: {
    //点击搜索按钮或者input回车按键
    search(select) {
      select === "baidu"
        ? this.baidu()
        : select === "sougou"
        ? this.sougou()
        : select === "weixin"
        ? this.weixin()
        : select === "zhihu"
        ? this.zhihu()
        : select === "weibo"
        ? this.weibo()
        : select === "douban"
        ? this.douban()
        : this.develop();
    },
    //跳转方法
    toWeb(website, searchPath) {
      if (this.keyword) {
        window.open(website + "/" + searchPath + this.keyword, "_blank");
        this.keyword = "";
      } else {
        window.open(website, "_blank");
      }
    },
    //开发者搜索
    develop() {
      let baidu = "https://kaifa.baidu.com";
      let searchPath = "searchPage?wd=";
      this.toWeb(baidu, searchPath);
    },
    //百度搜索
    baidu() {
      let baidu = "https://www.baidu.com";
      let searchPath = "s?ie=utf-8&f=8&rsv_bp=1&tn=baidu&wd=";
      this.toWeb(baidu, searchPath);
    },
    //搜狗搜索
    sougou() {
      let sougou = "https://www.sogou.com";
      let searchPath = "web?query=";
      this.toWeb(sougou, searchPath);
    },
    //微信搜索
    weixin() {
      let weixin = "http://weixin.sogou.com";
      let searchPath = "weixin?type=2&query=";
      this.toWeb(weixin, searchPath);
    },
    //知乎搜索
    zhihu() {
      let zhihu = "https://www.zhihu.com";
      let searchPath = "search?type=content&q=";
      this.toWeb(zhihu, searchPath);
    },
    //微博搜索
    weibo() {
      if (this.keyword) {
        window.open("http://s.weibo.com/weibo/" + this.keyword, "_blank");
        this.keyword = "";
        return false;
      } else {
        window.open("https://weibo.com/", "_blank");
        return false;
      }
    },
    //豆瓣搜索
    douban() {
      let douban = "https://www.douban.com";
      let searchPath = "search?source=suggest&q=";
      this.toWeb(douban, searchPath);
    },
  },
};
</script>

<style scoped>
.input_group {
  width: 100%;
}

.search {
  width: 61.8%;
  margin: auto;
  margin-top: 16.8%;
}
</style>
