import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home
  },
  {
    path: "/project/",
    name: "Project",
    component: () => import("../views/Project.vue"),
    children: [
      {
        path: '/project/resources',//以“/”开头的嵌套路径会被当作根路径，所以子路由上不用加“/”;在生成路由时，主路由上的path会被自动添加到子路由之前，所以子路由上的path不用在重新声明主路由上的path了。
        name: 'Resources',
        component: () => import('../views/projects/Resources.vue')
      },
    ]
  },
  {
    path: "/about",
    name: "About",
    component: () => import("../views/About.vue")
  },
  {
    path: "/markdown",
    name: "Markdown",
    component: () => import('../views/projects/MarkdownFile.vue')
  },
  {
    path: "/photoshop",
    name: "Photoshop",
    component: () => import("../views/Photoshop.vue")
  },
  {
    path: "/contact",
    name: "Contact",
    component: () => import("../views/Contact.vue")
  }
];

const router = new VueRouter({
  mode: 'history', //路由模式，取值为history与hash
  base: '/', //打包路径，默认为/，可以修改
  routes
});

export default router;
